import './form.less'
import Myheader from 'components/myheader/myheader.vue'
import Myflow from 'components/myflow/myflow.vue'
import Myfooter from 'components/myfooter/myfooter.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import Scroller from 'vux/src/components/scroller/index.vue'


/* eslint-disable*/
Vue.use(ToastPlugin)


new Vue({
el: '#body',
data: {
project: window.localStorage.getItem('project'), //项目
name: '',//姓名
sex: '',//性别
birthDate: '',//出生日期
bloodType: '',//血型
address: '',//居住地
Email: '',//邮箱
tel: '',//联系电话
ICE: '',//紧急联系人
ICETel: '',//紧急联系人电话
bloodTypeList: ['A', 'B']

},
created () {


},
methods: {
submit() {
this.$vux.toast.show({
text: '您已成功提交',
onHide () {
window.location.href = './pay.html'
}
})
},
change ()  {

}
},
components: {
Myheader,
Myflow,
Myfooter,
Cell,
Group,
XButton,
XInput,
Datetime,
PopupRadio,
ToastPlugin,
Scroller
}
})



